.App {
  text-align: center; 
  font-family: 'NanumSquareNeo', sans-serif; 
  background-color: #96B4DC; 
  width: 100%; 
  height: 100vh;
}


/* 헤더 스타일 */
.App-header {
  background-color: #282c34; /* 헤더 배경색 설정 */
  min-height: 100vh; /* 헤더 높이를 뷰포트 높이만큼 설정 */
  display: flex; /* 플렉스박스 레이아웃 사용 */
  flex-direction: column; /* 플렉스 항목을 수직 정렬 */
  align-items: center; /* 항목을 수평 중앙 정렬 */
  justify-content: center; /* 항목을 수직 중앙 정렬 */
  font-size: calc(10px + 2vmin); /* 글꼴 크기를 뷰포트 크기에 비례하여 설정 */
  color: white; 
}


/* 중앙 컨텐츠 스타일 */
.HeadContent {
  color: #F8E950; /* 글꼴 색상 설정 */
  margin: auto; /* 자동으로 중앙 정렬 */
  text-align: center; /* 텍스트 가운데 정렬 */
  position: absolute; /* 절대 위치 지정 */
  justify-content: center; /* 컨텐츠 중앙 정렬 */
  left: 50%; /* 수평 중앙 정렬 */
  top: 50%; /* 수직 중앙 정렬 */
  font-size: 30px; /* 글꼴 크기 설정 */
  transform: translate(-50%, -50%); /* 컨텐츠를 정확히 중앙에 배치 */
}

/* 섹션 스타일 */
.section {
  min-height: 100vh; /* 각 섹션의 높이를 뷰포트 높이만큼 설정 */
  display: flex; /* 플렉스박스 레이아웃 사용 */
  justify-content: center; /* 수평 중앙 정렬 */
  align-items: center; /* 수직 중앙 정렬 */
  transition: opacity 0.8s ease-out, transform 0.8s ease-out; /* 페이드인 및 슬라이드 효과 */
  opacity: 0; /* 처음에는 보이지 않게 설정 */
  transform: translateY(20px); /* 아래에서 위로 슬라이드 */
}

/* 보이는 섹션에 대한 스타일 */
.section.visible {
  opacity: 1; /* 보이도록 설정 */
  transform: translateY(0); /* 원래 위치로 이동 */
}

/* 숨겨진 섹션에 대한 스타일 */
.section.hidden {
  opacity: 0; /* 보이지 않게 설정 */
  transform: translateY(20px); /* 아래로 슬라이드 */
}

/* 각 섹션마다 다른 스타일링 */
.section1 {
  background-color: #96B4DC; /* 배경색 설정 */
  font-family: 'NanumSquareNeo', sans-serif; /* 폰트 설정 */
}

.section2 {
  background-color: #e67e22; /* 배경색 설정 */
}

.section3 {
  background-color: #16a085; /* 배경색 설정 */
}

/* 섹션별 텍스트 스타일 */
.section1 h1 {
  color: #fff; /* 글자 색상 설정 */
}

.section2 h1 {
  color: #000; /* 글자 색상 설정 */
}

.section3 h1 {
  color: #fff; /* 글자 색상 설정 */
}

/* 배경화면 컨테이너 스타일 */
.background {
  position: relative; /* 상대적 위치 지정 */
  width: 100%; /* 너비 100% */
  height: 100vh; /* 높이 100vh */
  background-color: #001d3d; /* 고정된 배경화면 색상 */
  overflow: hidden; /* 넘치는 콘텐츠 숨기기 */
}

/* 별 스타일 */
.star {
  position: absolute; /* 절대 위치 지정 */
  width: 0.3vh;; /* vh 설정으로 반응형 */
  height: 0.3vh; /* vh 설정으로 반응형 */
  background-color: #ffffff; /* 별의 색상 설정 */
  border-radius: 50%; /* 원형으로 만들기 */
  opacity: 0; /* 초기 투명도 설정 */
  animation: twinkling 2.5s infinite ease-in-out; /* 반짝이는 애니메이션 */
}

/* 반짝이는 애니메이션 정의 */
@keyframes twinkling {
  0%, 100% { opacity: 0; } /* 시작과 끝에서 투명도 0 */
  50% { opacity: 1; } /* 중간에서 투명도 1 */
}

/* 컨텐츠 스타일 */
.content {
  position: relative; /* 절대 위치 지정 */
  z-index: 1; /* 별보다 앞에 보이도록 설정 */
  color: white; /* 텍스트 색상 설정 */
  text-align: center; /* 텍스트 가운데 정렬 */
  margin-right: 30%;
  margin-top: 20%;
  font-size: 50px;
}

.content2 {
  position: relative; /* 절대 위치 지정 */
